  --footer-text-color: #545454;
  --background-color: #fff5e9;
  --time-function: cubic-bezier(.4, 0, .2, 1);
  --card-gap: 16px;
  --scroll-width: 25px;
}

body {
  background-color: var(--background-color);
  font-family: Roboto, sans-serif;
  font-style: normal;
}

body.modal-open {
