.backdrop {
  width: 100%;
  height: 100%;
  z-index: 90000;
  transition: opacity 250ms var(--time-function), visibility 250ms var(--time-function);
  background-color: rgba(0, 0, 0, .25);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is_hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.button {
  width: 152px;
  height: 44px;
  border: 1px solid var(--primary-white-color);
  color: var(--primary-white-color);
  text-transform: uppercase;
  transition: background-color 250ms var(--time-function), border 250ms var(--time-function);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  padding: 14px 0 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: inline-block;
}

.button:hover, .button:focus, .button.active {
  background-color: var(--button-activ-color);
  border: 1px solid var(--button-activ-color);
  filter: drop-shadow(0 8px 43px rgba(255, 107, 1, .6));
}

.button-modal {
  width: 110px;
  height: 44px;
  border: 1px solid var(--primary-text-color);
  color: var(--primary-text-color);
  background-color: var(--primary-white-color);
  text-transform: uppercase;
  transition: color 250ms var(--time-function), background-color 250ms var(--time-function), border 250ms var(--time-function);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 13px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: inline-flex;
}

.button-modal:hover, .button-modal:focus {
  background-color: var(--button-activ-color);
  border: 1px solid var(--button-activ-color);
  color: var(--primary-white-color);
