.loader {
  color: var(--button-activ-color);
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  background-color: currentColor;
  border-radius: 50%;
  font-size: 48px;
  position: relative;
  top: calc(50% - 172px);
  left: calc(50% - 32px);
  transform: rotateX(-60deg)perspective(1000px);
}

.loader:before, .loader:after {
  content: "";
  box-sizing: border-box;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  animation: flowerFlow 1s ease-out infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: .4s;
}

@keyframes flowerFlow {
  0% {
    opacity: 1;
    transform: rotate(0deg);
    box-shadow: 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em;
  }

  100% {
    opacity: 0;
    transform: rotate(180deg);
    box-shadow: -1em -1em 0 -.35em, 0 -1.5em 0 -.35em, 1em -1em 0 -.35em, -1.5em 0 0 -.35em, 1.5em 0 0 -.35em, -1em 1em 0 -.35em, 0 1.5em 0 -.35em, 1em 1em 0 -.35em;
  }
}

@keyframes shine {
  from {
    opacity: 0;
    left: 0%;
  }

  50% {
    opacity: 1;
  }

  to {
    opacity: 0;
    left: 100%;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)translate3d(0, 0, 0);
  }

  25% {
    transform: rotate(3deg)translate3d(0, 0, 0);
  }

  50% {
    transform: rotate(-3deg)translate3d(0, 0, 0);
  }

  75% {
    transform: rotate(1deg)translate3d(0, 0, 0);
  }

  100% {
    transform: rotate(0deg)translate3d(0, 0, 0);
  }
}

@keyframes jelly {
  25% {
    transform: scale(.9, 1.1);
  }

  50% {
    transform: scale(1.1, .9);
  }

  75% {
    transform: scale(.95, 1.05);
  }
}

@keyframes pulse {
  from {
    box-shadow: 0 0 0 0 var(--button-activ-color);
  }
}

